// Definiere den Basis-Transition-Wert
$transition-base: all 0.25s ease;

// Optional: Standardwerte fr die Bedingungsvariablen
$enable-transitions: true;
$enable-reduced-motion: true;

// stylelint-disable property-disallowed-list
/**
 * Mixin fr CSS Transitionen mit Untersttzung fr reduzierte Bewegung.
 * 
 * @param {List} $transition... - Eine oder mehrere Transition-Eigenschaften.
 */
@mixin transition($transition...) {
  // Setze Standard-Transition, wenn keine Argumente bergeben werden
  @if length($transition) == 0 {
    $transition: $transition-base;
  }

  // berprfe, ob mehrere Argumente bergeben wurden
  @if length($transition) > 1 {
    @each $value in $transition {
      @if $value == null or $value == none {
        @warn "Das Schlsselwort 'none' oder 'null' sollte nur als einzelnes Argument verwendet werden.";
      }
    }
  }

  // Wende Transitionen an, wenn aktiviert
  @if $enable-transitions {
    // Setze die Transition, wenn das erste Argument nicht null ist
    @if nth($transition, 1) != null {
      transition: $transition;
    }

    // Untersttze reduzierte Bewegungseinstellungen
    @if $enable-reduced-motion and nth($transition, 1) != null and nth($transition, 1) != none {
      @media (prefers-reduced-motion: reduce) {
        transition: none;
      }
    }
  }
}
